import React,{Component} from 'react';
// import logo from './logo.svg';
// import { Navbar,NavbarBrand } from 'reactstrap';
import Header from './HeaderComponent'
import Footer from './FooterComponent'
import Menu from './MenuComponent';
import DishdetailComponent from './DishdetailComponent';
import Home from './HomeComponent';
import {Switch,Route,Redirect,withRouter} from 'react-router-dom';
import Contact from './ContactComponent';
import About from './AboutComponent';
import {connect} from 'react-redux';
import {Card,CardImg,CardText,CardBody,CardTitle,Breadcrumb,BreadcrumbItem,Nav,NavItem,Button} from 'reactstrap';
import {postComment,fetchDishes,fetchComments,fetchPromos,fetchLeaders} from '../redux/ActionCreator';
import {actions} from 'react-redux-form';
import {TransitionGroup,CSSTransition} from 'react-transition-group';


const mapStateToProps=state=>{

  return{
    dishes:state.dishes,
    comments:state.comments,
    promotions:state.promotions,
    leaders:state.leaders
  }
}

const mapDispatchToProps=(dispatch)=>({
  postComment:(dishId,rating,author,comment)=>dispatch(postComment(dishId,rating,author,comment)),
  fetchDishes:()=>{dispatch(fetchDishes())},
  resetFeedbackForm:()=>{dispatch(actions.reset('feedback'))},
  fetchComments:()=>{dispatch(fetchComments())},
  fetchPromos:()=>{dispatch(fetchPromos())},
  fetchLeaders:()=>{dispatch(fetchLeaders())}

});
class Main extends Component{
  constructor(props){
    super(props);
  }
  componentDidMount(){
    this.props.fetchDishes();
    this.props.fetchComments();
    this.props.fetchPromos();
    this.props.fetchLeaders();

  }

  render(){
    //filterfunction supplies it as an array with only one element
    const HomePage=()=>{
      return(
        <Home dish={this.props.dishes.dishes.filter((dish)=>dish.featured)[0]}
        dishesLoading={this.props.dishes.isLoading}
        dishesErrMess={this.props.dishes.errMesss}
        promotions={this.props.promotions.promotions.filter((promotions)=>promotions.featured)[0]}
        promosLoading={this.props.promotions.isLoading}
        promosErrMess={this.props.promotions.errMesss}
        leaders={this.props.leaders.leaders.filter((leaders)=>leaders.featured)[0]}
        leadersLoading={this.props.leaders.isLoading}
        leadersErrMess={this.props.leaders.errMesss}

        />
        );
    }
  const DishWithId = ({match}) => {
    console.log('dishwithid');
    // console.log(comments);
      return(
          <DishdetailComponent 
          isLoading={this.props.dishes.isLoading}
          errMess={this.props.dishes.errMesss}
          comments={this.props.comments.comments.filter((comment) => comment.dishId === parseInt(match.params.dishId,10))}
          commentsErrMess={this.props.comments.errMesss}
          dish={this.props.dishes.dishes.filter((dish) => dish.id === parseInt(match.params.dishId,10))[0]} 
          postComment={this.props.postComment} />
      );
    };
    //exact is used to get when we exactly want the '/menu ' page
    return(
      <div> 
        <Header />
          <TransitionGroup>
            <CSSTransition key={this.props.location.key} classNames="page" timeout={300}>
                <Switch>
                  <Route path="/home" component={HomePage} />
                  <Route exact path="/menu" component={() => <Menu dishes={this.props.dishes}/>} />
                  <Route exact path='/contactus' component={() => <Contact resetFeedbackForm={this.props.resetFeedbackForm} />} />
                  <Route exact path="/menu/:dishId" component={DishWithId} />
                   <Route path="/aboutus" component={() => <About leaders={this.props.leaders}/>} />
                  <Redirect to="/home" />
                </Switch>
              </CSSTransition>
          </TransitionGroup>
      <Footer />
    </div>
      );
  }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Main));


















