import {Card, CardImg, CardText, CardTitle, CardBody, BreadcrumbItem, Breadcrumb, Button,
    Modal, ModalHeader, ModalBody, Row, Label, Col} from 'reactstrap';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { LocalForm, Control, Errors } from 'react-redux-form';
import {Loading} from './LoadingComponent';
import {baseUrl} from '../shared/sharedUrl';
import {FadeTransform,Fade,Stagger} from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);

class CommentForm extends Component {

    constructor(props) {
        super(props);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            isModalOpen : false,
        };
    }

    handleSubmit(values){
        
        this.props.postComment(this.props.dishId,values.rating,values.author,values.comment)
        this.toggleModal();
    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    render() {
        return (
            <React.Fragment>
                <Button outline onClick={this.toggleModal}><span className="fa fa-pencil fa-lg"></span> Submit Comment</Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group">
                                <div className="col-12">
                                    <Label htmlFor="rating">Rating</Label>
                                    <Control.select model=".rating" id="rating" name="rating"
                                        className="form-control">
                                            <option>5</option>
                                            <option>4</option>
                                            <option>3</option>
                                            <option>2</option>
                                            <option>1</option>
                                    </Control.select>
                                </div>
                            </Row>
                            <Row className="form-group">
                                <div className="col-12">
                                    <Label htmlFor="author">Your Name</Label>
                                    <Control.text model=".author" id="author" name="author"
                                        placeholder="Your Name" className="form-control"
                                        validators={{
                                            required, minLength: minLength(3), maxLength: maxLength(15)
                                        }}    
                                    />
                                    <Errors className="text-danger"
                                        model=".author"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                        }}                                    
                                    />
                                </div>                                        
                            </Row>
                            <Row className="form-group">
                                <div className="col-12">
                                    <Label htmlFor="message">Comment</Label>
                                    <Control.textarea model=".message" rows="6" name="message" id="message"
                                        className="form-control" />
                                </div>
                            </Row>
                            <Button type="submit" value="submit" color="primary">Submit</Button>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </React.Fragment>
        );
    }
}

function RenderDish({dish}) {
        if(dish != null)
        {
            return (
            <FadeTransform in 
         transformProps={{
            exitTransform:'scale(0.5) translateY(-50%)'
         }}>
                <Card>
                    <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle>{ dish.name }</CardTitle>
                        <CardText>{ dish.description }</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
            );
        }
        else {
            return (
                <div></div>
            );
        }
    }

function RenderComments({comments},postComment,dishId) {
        if(comments != null) {
            const dishComments = comments.map((currComment) => {
                return (
                 <Fade in>
                        <li key={currComment.id}>
                            <p>{ currComment.comment }</p>
                            <p>
                                --{ currComment.author } , { new Intl.DateTimeFormat('en-US', {year: 'numeric', month:'short', day:'2-digit'}).format(new Date(currComment.date))}
                            </p>
                        </li>
                </Fade>
                );
            });
            return (
                <React.Fragment>
                <h4>Comments</h4>
                <ul class="list-unstyled">
                    <Stagger in >
                            {dishComments	}
                            </Stagger>
                </ul>
                <CommentForm dishId={dishId} postComment={postComment}/>
                </React.Fragment>
            );
        }
        else {
            return (
                <div></div>
            );
        }
    }

const DishDetail = (props) => {
    if(props.isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if(props.errMess){
        return(
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>Menu</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish={props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m-1">
                        <RenderComments comments={props.comments}
                         postComment={props.postComment}
                         dishId={props.dish.id}/>
                    </div>
                </div>
            </div>
        );
    }

export default DishDetail;